@keyframes fadeInRight--rules__item {
  from {
    opacity: 0;
    transform: translateX(5rem);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes zoomIn--rules__item {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

@keyframes fadeInRotate--leaf-green {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  20% {
    opacity: 0.5;
    transform: rotate(12deg);
  }

  40% {
    opacity: 1;
    transform: rotate(-4deg);
  }

  60% {
    transform: rotate(4deg);
  }

  80% {
    transform: rotate(-4deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes fadeInRotate--leaf-blue-1 {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  20% {
    opacity: 0.5;
    transform: rotate(12deg);
  }

  40% {
    opacity: 1;
    transform: rotate(-3deg);
  }

  60% {
    transform: rotate(3deg);
  }

  80% {
    transform: rotate(-3deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes fadeInRotate--leaf-blue-2 {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  20% {
    opacity: 0.5;
    transform: rotate(-12deg);
  }

  40% {
    opacity: 1;
    transform: rotate(3deg);
  }

  60% {
    transform: rotate(-3deg);
  }

  80% {
    transform: rotate(3deg);
  }

  100% {
    transform: rotate(-3deg);
  }
}

@keyframes zoomIn--suitcase {
  0% {
    transform: translateY(100%) scale(0);
  }

  100% {
    transform: translateY(0) scale(1);
  }
}

@keyframes slideInUp--flamingo {
  0% {
    opacity: 0;
    transform: rotate(0) translateY(20rem);
  }

  20% {
    opacity: 1;
    transform: rotate(0) translateY(-1rem);
  }

  40% {
    opacity: 1;
    transform: rotate(2deg) translateY(1rem);
  }

  60% {
    transform: rotate(-2deg) translateY(-1rem);
  }

  80% {
    transform: rotate(2deg) translateY(1rem);
  }

  100% {
    transform: rotate(0) translateY(0);
  }
}

@keyframes expand--form--result {
  0% {
    opacity: 0;
    width: calc(10% - 23rem);
  }

  50% {
    opacity: 1;
  }
}

@keyframes fadeIn--form__button {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn-controls {
  0% {
    opacity: 0.2;
  }

  100% {
    opacity: 1;
  }
}

@keyframes roll-control-prev {
  0% {
    opacity: 0;
    transform: translateX(-100%) rotate(-100deg);
  }

  100% {
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes roll-control-next {
  0% {
    opacity: 0;
    transform: translateX(100%) rotate(100deg);
  }

  100% {
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes rollInRight-result-button {
  0% {
    transform: translateX(100%) rotate(100deg);
  }

  100% {
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes fadeIn-result-button {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
